var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"window-height content-style"},[_c('h3',[_vm._v("Culture: Being Forgotten? & The Main RRL")]),_c('h5',{staticClass:"video-analysis"},[_vm._v("Analysis")]),_c('span',[_vm._v(" The text discusses how most Filipino’s of today’s generation have forgotten their own culture and have changed due to technological advancements and colonial mentality of the Philippine Society. It talks about how culture is not just about the arts but also the society’s way of thinking and modes of life. The text ends strong by asking the reader to stop mimicking other cultures and instead rebirth our own culture as Filipinos. What is interesting about this topic is that it uses the word mimicking specifically, and mimicking would entail copying every single aspect. As the Philippines have been colonized by many countries, some leaving more of an impact than others, it’s impossible to say that we must abolish all “borrowed” culture norms from the other countries. This is because our culture what it is today, is a mixture of many different cultures, this can be seen in our cuisine, language etc. The article only wishes to further drive the point that our true native culture is being lost in the borrowed culture and we shouldn’t be mimicking instead building upon them. ")]),_c('h5',{staticClass:"video-analysis"},[_vm._v("Reference")]),_c('b-link',{staticClass:"main-link",attrs:{"href":"https://uglywriters.com/2018/01/30/culture-being-forgotten/"}},[_vm._v(" CULTURE: Being Forgotten?")]),_c('br'),_c('b-link',{staticClass:"main-link",attrs:{"href":"https://notforselle.wordpress.com/2019/05/14/how-can-ph-narratives-of-antiquity-be-made-relevant-to-contemporary-ph-life-culture-and-concerns/?fbclid=IwAR2pgHwUB9HxuEPZLkKAfYOZGs_Twpm3Ry9DOEvFZ7yNBZjIiiBSkUHqI-w"}},[_vm._v("How can PH narratives of antiquity be made relevant to contemporary PH life, culture and concerns?")])],1)}
